*{
    margin:0;
    padding:0;
}
html {
    font-size: 62.5%;
    width:100vw;
    height:100vh;
}
body {
    width:100%;
    height:100%;
    padding:2rem;
    box-sizing:border-box;
}

#root {
    background: linear-gradient(to bottom, #b2b2b2,#909090);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width:100%;
    height:100%;
}
h1,h2,h3,h4,h5,h6 {
    font-family: 'Days One',sans-serif;
}
p {
    font-family: 'Open Sans Condensed',sans-serif;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap:nowrap;
          flex-wrap:nowrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding:2rem 2rem 0rem;
  border:1px dotted #fbfcfc; 
}
.game-board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.dashboard {
  width:30rem;
  padding:2rem;
  text-align: center;
}
.dashboard h2 {
  text-transform: uppercase;
  padding-bottom:1rem;
  font-size: 1.5rem;
  
}
.snake-logo {
  font-size: 5rem;
  line-height: 10rem;
}
.score {
  margin-bottom:1rem;
}
.dashboard p {
  font-size: 2rem;
}

.reset-game,.try-again {
  background-color:#9dd6f9;
  border:1px solid #5eb6ef;
  outline:none;
  cursor:pointer;
  padding:1rem 1.5rem;
  color:#333333;
  font-weight: 700;
  font-size: 1.5rem;
}
.reset-game:hover,.try-again:hover{
  background-color:#5eb6ef;
  border:1px solid #9dd6f9;
  color: #333333;
}

.game-board h1 {
  text-transform: uppercase;
  padding-bottom:1.5rem;
  text-align: center;
  font-size: 2rem;
}
.board {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width:60rem;
  height:60rem;
  padding:2rem;
  box-shadow: 0px 0px 4px 2px rgba(0,0,0,0.3);
}
.cell {
  width:3rem;
  height:3rem;
  outline:1px solid #00bb43;
  background-color: #009b36;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  font-family: 'Open Sans Condensed',sans-serif;
  text-align: center;
  line-height: 3rem;
  font-size: 1rem;
  color:#b7e2fd;
}
.cell.apple {
  background-color: red;
}
.cell.snake-head {
  background-color: #19241a;
}
.cell.snake-tail {
  background-color: #076326;
}
.tips {
  min-height: 5rem;
  padding:1rem;
  text-align: center;
  font-size: 2rem;
  line-height: 5rem;
}
.result-board {
  width:100%;
  height: 100%;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
          flex-direction:column;
  -webkit-align-items:center;
          align-items:center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #333333;
}
.result-board .game-over {
  font-size: 4rem;
  font-weight: 700;
  font-family: 'Days One',sans-serif;
  color:#fbfcfc;
  margin-bottom:1rem;
}
.result-board .game-pad{
  font-size: 3.2rem;
  margin-bottom:2rem;
}

